:root {
  --field: #574d43;
  --lake: #0b6683;
}

body {
  height: 100vh;
  max-width: 100vw;
  background: #f7f6f2;
  margin: 0;
  padding: 0;
  font-family: Montserrat, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

h1, h2, h3, h4, h5, h6, p, button, input {
  color: var(--lake);
  margin: 0;
  font-family: Montserrat, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

main {
  z-index: 120;
  height: calc(100% - 4rem);
  grid-template-columns: 28rem 1fr;
  gap: 1rem;
  margin: 0;
  padding: 2rem;
  display: grid;
  position: relative;
  overflow: hidden;
}

.sidebar {
  border: 4px solid var(--field);
  background: #ffffff80;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  display: flex;
  overflow: auto;
}

#upload-form {
  flex-direction: column;
  flex: 1;
  gap: 1rem;
  display: flex;
}

button {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5px;
  color: #fff;
  cursor: pointer;
  border: 2px solid #0000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: .75rem 1rem;
  transition: transform .24s;
  display: inline-flex;
  transform: translateY(0);
}

button:hover {
  border: 2px solid #fff;
  transform: translateY(-2px);
}

button:disabled {
  cursor: not-allowed;
  background: #a5a5a5;
}

button:disabled:hover {
  transform: none;
}

#preview {
  max-width: 100%;
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-footer {
  background: #fff;
  gap: .5rem;
  margin: auto -1rem -1rem;
  padding: .5rem 1rem;
  display: flex;
  position: sticky;
  bottom: -1rem;
}

#submit-route-btn {
  border: 2px solid #fff;
  flex: 1;
  align-items: center;
  padding: 1rem;
  font-size: 1rem;
  position: sticky;
  bottom: 0;
}

#clear-btn {
  color: initial;
  background: #fff;
  flex: 1;
  margin-top: auto;
  padding: 1rem;
}

.preview__card {
  max-width: 100%;
  max-height: 9rem;
  border: 2px solid var(--field);
  background: #fff;
  grid-template-columns: 8rem 1fr;
  align-items: flex-start;
  transition: all .24s;
  display: grid;
  position: relative;
  overflow: hidden;
}

.preview__card:hover {
  cursor: pointer;
  transform: translateY(-2px);
  box-shadow: 0 12px 12px -4px #0000001a, 0 12px 8px -1px #0000000f;
}

.preview__card--remove-btn {
  color: #656565;
  opacity: .5;
  box-shadow: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  position: absolute;
  top: .25rem;
  right: .25rem;
}

.preview__card--remove-btn:hover {
  color: #820000;
  border: none;
  transform: translateY(0);
}

.location__card--remove-btn {
  color: #656565;
  opacity: .5;
  box-shadow: none;
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  position: absolute;
  top: .25rem;
  right: .25rem;
}

.location__card--remove-btn:hover {
  color: #820000;
  border: none;
  transform: translateY(0);
}

.preview__card--header {
  padding: .5rem;
  overflow: hidden;
}

.preview__card--text {
  padding: .75rem;
  font-size: .75rem;
  overflow: hidden;
}

.preview__card--text h4 {
  text-overflow: ellipsis;
  margin: 0;
  line-height: 1.75;
  overflow: hidden;
}

.preview__card--route {
  border-left: 8px solid var(--lake);
  box-shadow: none;
  border-radius: 0;
  margin: 0;
}

.preview__card--route:hover {
  cursor: initial;
  box-shadow: none;
  transform: none;
}

.preview__card--text dl {
  grid-template-columns: 4rem 1fr;
  align-items: baseline;
  gap: .25rem;
  margin: 0;
  padding: 0;
  display: grid;
}

.preview__card--text dt {
  text-transform: uppercase;
  letter-spacing: .25px;
  color: gray;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

.preview__card--text dd {
  margin: 0;
  padding: 0;
}

.preview__image {
  max-width: 100%;
  max-height: 5rem;
  border: 2px solid var(--field);
}

.preview__image:after {
  content: "";
  height: 100%;
  width: 100%;
  display: block;
  position: absolute;
  top: 0;
  box-shadow: inset 3px 3px 10px #000;
}

.sidebar h1 {
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  border-top: 1px solid var(--lake);
  border-bottom: 2px solid var(--lake);
  margin: .5rem 0;
  padding: 0;
  font-size: 2.25rem;
}

.sidebar label {
  font-size: .875rem;
  display: inline;
}

.carto {
  border: 4px solid var(--field);
  background: #faf4e5;
  padding: 1rem;
  overflow: hidden;
}

#map {
  height: 100%;
  z-index: 100;
  border: 1px dashed var(--field);
}

.leaflet-popup-content-photo {
  margin: .5rem;
}

.marker-photo {
  max-width: 100%;
  min-width: 8rem;
}

